import React, { Component } from 'react';
// import { useParams } from 'react-router-dom';

// import Modal from '../components/Modal/Modal';
// import Backdrop from '../components/Backdrop/Backdrop';
import JobSingleList from '../components/Jobs/JobList/JobSingleList';
import Spinner from '../components/Spinner/Spinner';
import AuthContext from '../context/auth-context';
import './Jobs.css';

class JobItemPage extends Component {
  state = {
    creating: false,
    jobs: [],
    isLoading: false,
    selectedJob: null,
  };
  isActive = true;

  static contextType = AuthContext;

  /*
  constructor(props) {
    super(props);
    this.titleElRef = React.createRef();
    this.wageElRef = React.createRef();
    this.dateElRef = React.createRef();
    this.descriptionElRef = React.createRef();
  }
*/
  componentDidMount() {
    this.fetchJobs();
    // console.log('didmount this.state.jobs:', this.state.jobs);

    // console.log('jobId:', this.jobId);
  }

  startCreateJobHandler = () => {
    this.setState({ creating: true });
  };

  fetchJobs() {
    this.setState({ isLoading: true });

    const { jobId } = this.props.match.params;

    // console.log('jobId :', jobId);

    const requestBody = {
      query: `
          query {
            jobItem(jobId: "${jobId}") {
              _id
              title
              description
              date
              wage
              creator {
                _id
                email
              }
            }
          }
        `,
    };
    // console.log('requestBody:', JSON.stringify(requestBody));
    fetch(`${process.env.REACT_APP_BACKEND_URL}/graphql`, {
      method: 'POST',
      body: JSON.stringify(requestBody),
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then((res) => {
        if (res.status !== 200 && res.status !== 201) {
          throw new Error('Failed: ' + res.status);
        }
        return res.json();
      })
      .then((resData) => {
        // console.log('resData:', resData);
        const job = resData.data.jobItem;
        // console.log('job: ', job);
        if (this.isActive) {
          this.setState({
            jobs: [job],
            selectedJob: job._id,
            isLoading: false,
          });
        }

        // this.setState((prevState) => {
        //   const updatedJobs = [];
        //   const job = resData.data.jobItem;
        //   updatedJobs.push(job);
        //   // return { jobs: updatedJobs };
        // });

        // console.log('this.state: ', this.state);
        // return this.state;
      })
      .catch((err) => {
        console.log('err:', err);
        if (this.isActive) {
          this.setState({ isLoading: false });
        }
      });
  }

  savedJobHandler = (jobId) => {
    if (!this.context.token) {
      this.setState({ selectedJob: null });
      return;
    }
    this.setState({ selectedJob: jobId });

    // console.log('this.state.selectedJob: ', this.state.selectedJob);
    const requestBody = {
      query: `
          mutation SavedJob($id: ID!) {
            savedJob(jobId: $id) {
              _id
             createdAt
             updatedAt
            }
          }
        `,
      variables: {
        id: jobId /*this.state.selectedJob._id,*/,
      },
    };

    fetch(`${process.env.REACT_APP_BACKEND_URL}/graphql`, {
      method: 'POST',
      body: JSON.stringify(requestBody),
      headers: {
        'Content-Type': 'application/json',
        Authorization: 'Bearer ' + this.context.token,
      },
    })
      .then((res) => {
        if (res.status !== 200 && res.status !== 201) {
          throw new Error('Failed!');
        }
        return res.json();
      })
      .then((resData) => {
        // console.log('resData: ', resData);
        this.setState({ selectedJob: null });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  componentWillUnmount() {
    this.isActive = false;
  }

  render() {
    return (
      <React.Fragment>
        {this.state.isLoading ? (
          <Spinner />
        ) : (
          <JobSingleList
            jobs={this.state.jobs}
            authUserId={this.context.userId}
            onViewDetail={this.showDetailHandler}
            onSaved={this.savedJobHandler}
          />
        )}
      </React.Fragment>
    );
  }
}

export default JobItemPage;
