import React, { Component } from 'react';
import { BrowserRouter, Route, Redirect, Switch } from 'react-router-dom';

import AuthPage from './pages/Auth';
import SavedsPage from './pages/Saveds';
import JobsPage from './pages/Jobs';
import AddJobPage from './pages/AddJobPage';
import JobItem from './pages/JobItem';
import AboutPage from './pages/AboutPage';
import MainNavigation from './components/Navigation/MainNavigation';
import Footer from './components/Navigation/Footer';
import AuthContext from './context/auth-context';

import './App.css';

class App extends Component {
  state = {
    token: null,
    userId: null,
    email: null,
  };

  login = (token, userId, email, tokenExpiration) => {
    this.setState({ token: token, userId: userId, email: email });
  };

  logout = () => {
    this.setState({ token: null, userId: null, email: null });
  };

  render() {
    return (
      <BrowserRouter>
        <React.Fragment>
          <AuthContext.Provider
            value={{
              token: this.state.token,
              userId: this.state.userId,
              email: this.state.email,
              login: this.login,
              logout: this.logout,
            }}
          >
            <MainNavigation />
            <main className='main-content'>
              <Switch>
                <Redirect from='/' to='/jobs' exact />
                {this.state.token && <Redirect from='/auth' to='/jobs' exact />}
                {!this.state.token && (
                  <Route path='/auth' component={AuthPage} />
                )}
                {this.state.token && (
                  <Route path='/job/add' component={AddJobPage} />
                )}
                <Route path='/jobs' component={JobsPage} />
                <Route path='/job/:jobId' component={JobItem} exact />
                <Route path='/about' component={AboutPage} exact />

                {this.state.token && (
                  <Route path='/saveds' component={SavedsPage} />
                )}
                {!this.state.token && <Redirect to='/auth' exact />}
              </Switch>
            </main>
            <Footer />
          </AuthContext.Provider>
        </React.Fragment>
      </BrowserRouter>
    );
  }
}

export default App;
